import Swal from "sweetalert2";
import { fecthWithToken } from "../helpers/fetch";
import { prepareEvents } from "../helpers/prepareEvents";
import { types } from "../types/types";

export const  eventStartAddNew = (event) =>{
    return async(dispatch, getState) =>{  

        const {uid,name} = getState().auth;

        try {

        const resp = await fecthWithToken('events',event,'POST');
        const body = await resp.json();

        if (body.ok) {
            event.id = body.event.id;
            event.user = {
                _id: uid,
                name
            }

            dispatch(eventAddNew(event))
        }
            
        } catch (error) {
            console.log(error)
        }
    }
} 

const eventAddNew = (event) =>({
    type:types.eventAddNew,
    payload: event
});

export const eventSetActive = (event) =>({
    type:types.eventSetActive,
    payload: event
});

export const eventClearActiveEvent = () =>({
    type: types.eventClearActiveEvent
})

export const eventStartUpdate = (event) => {
    return async(dispach) =>{
        try {
            const resp = await fecthWithToken(`events/${event.id}`, event, 'PUT');
            const body = await resp.json();

            if(body.ok){
                dispach(eventUpdate(event))
            }else{
                Swal.fire('Error', body.msg,'error')
            }

        } catch (error) {
            console.log(error)
        }
    }
}

const eventUpdate = (event) =>({
    type: types.eventUpdated,
    payload: event
})


export const eventStartDelete = () =>{
    return async(dispach, getState) =>{

        const {id} = getState().calendar.activeEvent

        try {
            const resp = await fecthWithToken(`events/${id}`, {}, 'Delete');
            const body = await resp.json();

            if(body.ok){
                dispach(eventDeleted())
            }else{
                Swal.fire('Error', body.msg,'error')
            }

        } catch (error) {
            console.log(error)
        }
    }
}

const eventDeleted = () =>({
    type: types.eventDeleted
})

export const eventStartLoading = () =>{
    return async(dispach) =>{
        
        try {

            const resp = await fecthWithToken('events');
            const body = await resp.json();

            const events  = prepareEvents(body.event);
            dispach(eventLoaded(events))
            
        } catch (error) {
            console.log(error)
        }
    }
}

const eventLoaded = (events) =>({
    type: types.eventLoaded,
    payload: events
})

export const eventLogout = () =>({
    type: types.eventLogout
})


